import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';

const Attendance = () => (
  <StaticQuery
    query={graphql`
      query {
        art_nico: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "nico" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="attendance">
        <Container>
          <Grid>
            <div>
              <h2>Teilnahme</h2>
              <p>
                Wenn Du schon weit, ob Du an unserer Feier teilnehmen kannst oder auch nicht,
                trage bitte Deinen Vor- und Nachnamen sowie Deine Auswahl in diese <a href="https://doodle.com/poll/gmushagszfw7hmfe" target="_blank">Liste</a> ein.
              </p>
            </div>
            <Art>
              <Img fluid={data.art_nico.childImageSharp.fluid} />
            </Art>
          </Grid>
        </Container>
      </Section>
    )}
  />
);

const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 2fr;
  grid-gap: 40px;
  text-align: right;
  align-items: center;
  justify-items: center;
  margin: 24px 0;

  ${props =>
    props.inverse &&
    `
    text-align: left;
    grid-template-columns: 2fr 3fr;
  `}

  h2 {
    margin-bottom: 16px;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }

    ${props =>
    props.inverse &&
    `
        ${Art} {
          order: 2;
        }
    `}
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 380px;
  width: 100%;
`;

export default Attendance;
